const httpApi = {}
import axios from 'axios'

// 注意端口号是 内网是8089， 映射到外网是8088
// httpApi.baseUrl = 'https://rtxxdj.linewell.com/prod-api-baoantegong'  // 打包宝安特供的地址
// httpApi.baseUrl = 'http://116.7.225.237:8088/prod-api'  // 宝安特供 外网地址的 
// httpApi.baseUrl = 'http://192.9.206.22:8089/prod-api'  // 宝安特供 内网地址的


httpApi.baseUrl = 'https://rtxxdj.linewell.com/prod-api-tongyong'  // 打包产品的地址
// httpApi.baseUrl = 'http://192.9.206.22:8199/prod-api' // 产品 内网地址
// httpApi.baseUrl = 'http://116.7.225.237:8093/prod-api' // 产品 内网地址

httpApi.appid = 'afc903d9d5e449488a3cf9a00c3d1294'
httpApi.appkey = '8cb4f61b-e89a-405b-acc8-4d992826c247'

httpApi.get = ((url, params) => {
    return new Promise((resolve, reject) => {
        let fullUrl = httpApi.baseUrl + url;

        let data = JSON.stringify(params);
        let config = {
            method: 'get',
            url: fullUrl,
            headers: {
                "appid": httpApi.appid,
                "appkey": httpApi.appkey,
                'Content-Type': 'application/json'
            },
            data: data
        };
        // console.log("请求参数:", fullUrl, params)
        axios(config)
            .then(function (response) {
                resolve(response.data);
            })
            .catch(function (error) {
                console.error("请求错误:", error)
                reject(error);
            });
    })

})

httpApi.post = ((url, params) => {
    return new Promise((resolve, reject) => {
        let fullUrl = httpApi.baseUrl + url;
        // console.log(params);
        // let data = JSON.stringify(params);
        // console.log(data);
        let config = {
            method: 'post',
            url: fullUrl,
            headers: {
                "appid": httpApi.appid,
                "appkey": httpApi.appkey,
                'Content-Type': 'application/json'
            },
            data: params,
        };
        // console.log("请求参数:", fullUrl, params)
        axios(config)
            .then(function (response) {
                console.log("请求结果:", response.data);
                resolve(response.data);
            })
            .catch(function (error) {
                console.log("请求错误:", error)
                reject(error);
            });
    })

})

export default httpApi