import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        currentTab: 0,
    },
    getters: {
        currentTab: state => {
            return state.currentTab
        }
    },
    mutations: {
        updateCurrentTab(state, index) {
            state.currentTab = index
        }
    },
    actions: {},
    modules: {}
});